@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  font-family: "Quicksand", sans-serif;
}

.App {
  min-height: 100vh;
  width: 100vw;
}

header {
  height: 10vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

header h1 {
  margin-left: 50px;
  letter-spacing: 2px;
  font-size: 30px;
}

header button {
  margin-right: 50px;
  background-color: white;
  border: none;
}

header button img {
  height: 60px;
  width: 60px;
}

header button img:hover {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}

.main-container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

.dark-mode {
  background-color: rgb(60, 60, 60);
  color: white;
}

.dark-mode button {
  background-color: rgb(60, 60, 60);
}

/* MEDIA QUERIES */

@media screen and (max-width: 1050px) {
  .main-container {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }
}

@media screen and (max-width: 650px) {
  header h1 {
    font-size: 18px;
  }
  header button img {
    width: 40px;
    height: 40px;
  }
}

.music-player {
  width: 75%;
  min-height: 90vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.img-container {
  width: 550px;
  height: 550px;
  border-radius: 50%;
  overflow: hidden;
  border: 10px solid white;
}

.img-container img {
  width: 550px;
  height: 550px;
  object-fit: cover;
}

.player-info {
  display: -webkit-flex;
  display: flex;
  height: 10vh;
  margin-top: 50px;
  -webkit-align-items: center;
          align-items: center;
}

.player-info h3 {
  font-size: 26px;
}

.player-info button {
  border: none;
  background-color: white;
  margin-left: 40px;
}

.player-info button img {
  width: 30px;
}

.player-info button img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

/* DARK MODE */

.dark-mode button {
  background-color: rgb(60, 60, 60);
}

.dark-mode .img-container {
  border: 10px solid rgb(60, 60, 60);
}

/* KEYFRAMES */

.play-mode {
  -webkit-animation: playing 3s infinite ease-in;
          animation: playing 3s infinite ease-in;
}

.play-mode img {
  -webkit-animation: scale 10s infinite;
          animation: scale 10s infinite;
}

@-webkit-keyframes playing {
  0% {
  }
  20% {
    border-top: 10px solid rgb(213, 238, 253);
  }
  25% {
    border-top: 10px solid rgb(144, 209, 250);
  }
  30% {
    border-left: 10px solid rgb(157, 214, 250);
  }
  50% {
    border-bottom: 10px solid rgb(171, 220, 250);
  }
  70% {
    border-right: 10px solid rgb(201, 233, 253);
  }
  80% {
    border-top: 10px solid rgb(213, 238, 253);
  }
  100% {
  }
}

@keyframes playing {
  0% {
  }
  20% {
    border-top: 10px solid rgb(213, 238, 253);
  }
  25% {
    border-top: 10px solid rgb(144, 209, 250);
  }
  30% {
    border-left: 10px solid rgb(157, 214, 250);
  }
  50% {
    border-bottom: 10px solid rgb(171, 220, 250);
  }
  70% {
    border-right: 10px solid rgb(201, 233, 253);
  }
  80% {
    border-top: 10px solid rgb(213, 238, 253);
  }
  100% {
  }
}

@-webkit-keyframes scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/* MEDIA QUERIES */

@media screen and (max-width: 1050px) {
  .music-player {
    width: 100%;
    min-height: 50vh;
    margin-top: 130px;
  }
}

@media screen and (max-width: 650px) {
  .music-player {
    margin-top: 80px;
  }
  .img-container {
    width: 300px;
    height: 300px;
  }
  .img-container img {
    width: 300px;
    height: 300px;
  }
  .player-info {
    margin-top: 20px;
  }
  .player-info h3 {
    font-size: 20px;
  }
}

.sounds-panel {
  width: 25%;
  min-height: 90vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.btn {
  margin: 30px 0px;
}

.btn button {
  padding: 15px;
  font-family: "Quicksand", sans-serif;
  font-size: 18px;
  font-weight: 600;
  background-color: white;
  border: 4px solid pink;
  border-radius: 10px;
}

.btn button:hover {
  background-color: pink;
  color: white;
}

/* DARK MODE */

.dark-mode button {
  background-color: rgb(60, 60, 60);
  color: white;
}

.dark-mode button:hover {
  color: black;
}

/* MEDIA QUERIES */

@media screen and (max-width: 1050px) {
  .sounds-panel {
    width: 100%;
    min-height: 15vh;
    margin-top: 100px;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .btn {
    margin: 20px 50px;
  }
}

@media screen and (max-width: 650px) {
  .sounds-panel {
    margin-top: 80px;
  }
  .btn {
    margin: 15px 10px;
  }
}

